@import-normalize;
.Description {
  margin-left: 134px;
  margin-top: 155px;
  display: flex;
  flex-direction: column;
  width: 533px;
  height: 507px;
  padding-bottom: 70px;
}

.First-Title {
  margin: 0;
  width: 533px;
  height: 231px;
  left: 134px;
  font-weight: 800;
  font-size: 64px;
  line-height: 77px;
  color: #ffffff;
}

.First-Parag {
  margin-top: 7px;
  width: 525px;
  height: 66px;
  left: 135px;
  top: 425px;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #fefefe;
}

.First-Button {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 79px;
  width: 274px;
  height: 64px;
  background: #f53627;
  border: 4px solid #ffffff;
  box-sizing: border-box;
  box-shadow: 0px 7px 12px rgba(0, 0, 0, 0.25);
  border-radius: 32px;
  font-weight: 600;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
}

.First-Button:hover {
  width: 284px;
  height: 70px;
  font-size: 20px;
  box-shadow: 0px 7px 15px rgba(0, 0, 0, 0.25);
  background: #f1463a;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .Description {
    margin-left: 36px;
    margin-top: 100px;
    width: 360px;
    height: 507px;
    padding-bottom: 0px;
  }
  .First-Title {
    margin: 0;
    width: 320px;
    height: 200px;
    left: 134px;
    font-size: 40px;
    line-height: 60px;
  }
  .First-Parag {
    margin-top: 0px;
    width: 320px;
    height: 66px;
    font-size: 18px;
    line-height: 22px;
  }
}
.First-Button {
  display: flex;
  margin-top: 95px;
}
