@import url("https://fonts.googleapis.com/css2?family=Inter&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Manrope&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans&display=swap");
.App {
  display: flex;
  flex-direction: column;
  font-family: "Inter";
  font-style: normal;
}

.FirstSection {
  width: 1440px;
  height: 759px;
  display: flex;
  flex-direction: column;
  background-color: #ff4029;
  position: relative;
  z-index: 1;
}

.NavBar {
  z-index: 5;
}

.SecondSection {
  width: 1440px;
  height: 659px;
  background-size: contain;
  background-repeat: no-repeat;
  background-color: rgba(240, 151, 151, 0.2);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.SmallCards {
  display: flex;
}
.BigCardsWrapper {
  margin-top: 46px;
  height: 211px;
  width: 1171px;
  display: flex;
}
.ThirdSection {
  width: 1440px;
  height: 326px;
  background: linear-gradient(262.18deg, #ffb737 -0.11%, #ffbe4b 97.74%);
}
.PhoneWrapper {
  position: relative;
  display: flex;
}
.phone1 {
  position: absolute;
  width: 247px;
  height: 495px;
  left: 134px;
  top: -43px;
  z-index: 2;
}
.phone2 {
  position: absolute;
  width: 213px;
  height: 429px;
  left: 322px;
  top: -10px;
  z-index: 1;
}
.ThirdSectionTexts {
  display: flex;
  height: 226px;
  flex-direction: column;
  margin-left: 611px;
}
.ThirdSectionParag {
  width: 460px;
  height: 144px;
  font-weight: 700;
  font-size: 40px;
  line-height: 48px;
  text-transform: capitalize;
  color: #ffffff;
}
.Download {
  width: 400px;
  display: flex;
}

.ios {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 39px;
  width: 93.5px;
  border-radius: 5px;
  background-color: #000;
  padding-left: 41px;
}
.apple {
  position: absolute;
  width: 19.72px;
  height: 16.52px;
  left: 17.13px;
  top: 13.1px;
}

.stem {
  position: absolute;
  width: 4.32px;
  height: 6.08px;
  left: 25.95px;
  top: 7.2px;
}
.iosWriting {
  /* display: flex; */
  /* flex-direction: column; */
  margin-bottom: 100px;
}

.iosSmallTitle {
  color: #fff;
  font-size: 9px;
  font-weight: 300;
}
.iosBigTitle {
  margin-top: -10px;
  margin-bottom: 20px;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
}

.play {
  margin-left: 40px;
  height: 40px;
  width: 135px;
}

@media only screen and (max-width: 600px) {
  .FirstSection {
    max-width: 360px;
    height: 568px;
    margin-bottom: -180px;
  }
  .FirstSec {
    z-index: 5;
  }
  .SecondSection {
    z-index: 2;
    max-width: 360px;
    height: 1350px;
    background-repeat: repeat;
    justify-content: center;
    align-items: unset;
  }
  .SmallCards {
    margin-top: -20px;
    flex-direction: column;
    margin-bottom: 225px;
  }
  .BigCardsWrapper {
    margin-top: -190px;
    width: 360px;
    flex-direction: column;
  }
}
