.WhiteCards {
  margin-left: 20px;
  position: relative;
  display: flex;
  width: 377px;
  height: 189px;
  background: #ffffff;
  box-shadow: 7px 10px 24px rgba(255, 199, 31, 0.22);
  border-radius: 12px;
}

.CardText {
  margin-left: 22px;
  width: 150px;
  display: flex;
  flex-direction: column;
}

.WhiteTitle {
  margin-bottom: 0px;
  width: 162px;
  height: 58px;
  font-weight: 800;
  font-size: 24px;
  line-height: 29px;
  color: #000000;
}
.WhiteName {
  margin-bottom: 18px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #868686;
}
.WhiteButton {
  width: 125px;
  height: 36px;
  background: #f73d27;
  border-radius: 18px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
  border: none;
}

.WhiteButton:hover .WhiteButtonText {
  display: none;
}
.WhiteButton:hover:before {
  display: flex;
  justify-content: center;
  align-items: center;
  content: "Add  ðŸ›’";
  margin-left: -10px;
  margin-top: -10px;
  width: 140px;
  height: 50px;
  font-size: 22px;
  border-radius: 18px;
  background-color: #f73d27;
  box-shadow: 0px 7px 12px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}
.CardCircle {
  position: absolute;
  top: 13px;
  right: 13px;
  width: 154px;
  height: 154px;
  background: #ffc71f;
  border-radius: 50%;
}

.CardImg {
  width: 195px;
  height: 155px;
  position: absolute;
  top: 5px;
  right: 10px;
}

@media only screen and (max-width: 600px) {
  .WhiteCards {
    margin-left: 20px;
    width: 320px;
    height: 189px;
  }
  .CardCircle {
    width: 120px;
    height: 120px;
  }
  .CardImg {
    width: 145px;
    height: 115px;
    top: 5px;
    right: 10px;
  }
}
