.RedCards {
  margin-left: 20px;
  position: relative;
  display: flex;
  width: 377px;
  height: 189px;
  background: #f22d2a;
  box-shadow: 7px 10px 24px rgba(255, 199, 31, 0.22);
  border-radius: 12px;
}

.CardText {
  margin-left: 22px;
  width: 150px;
  display: flex;
  flex-direction: column;
}

.RedTitle {
  margin-bottom: 0px;
  width: 146px;
  height: 58px;
  font-weight: 800;
  font-size: 24px;
  line-height: 29px;
  color: white;
}
.RedName {
  margin-bottom: 18px;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  color: #fff;
}
.RedButton {
  width: 125px;
  height: 36px;
  background: #ffc71f;
  border-radius: 18px;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  color: #000000;
  border: none;
}
.RedButton:hover .RedButtonText {
  display: none;
}
.RedButton:hover:before {
  display: flex;
  justify-content: center;
  align-items: center;
  content: "Add  ðŸ›’";
  margin-left: -10px;
  margin-top: -10px;
  width: 140px;
  height: 50px;
  font-size: 22px;
  border-radius: 18px;
  background-color: #ffc71f;
  box-shadow: 0px 7px 12px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}
.RedCardCircle {
  position: absolute;
  top: 13px;
  right: 13px;
  width: 154px;
  height: 154px;
  background: #fff;
  border-radius: 50%;
}

.RedCardImg {
  width: 230px;
  height: 180px;
  position: absolute;
  top: 0px;
  right: -10px;
}

@media only screen and (max-width: 600px) {
  .RedCards {
    margin-left: 20px;
    width: 320px;
    height: 189px;
    margin-top: 20px;
    margin-bottom: 20px;
  }
}
