.Contact {
  display: flex;
  width: 1440px;
  height: 120px;
  background: #f02228;
}

.Kfclogo {
  margin-top: 40px;
  margin-left: 135px;
  width: 132px;
  height: 39px;
}

.whatsapp {
  width: 45px;
  height: 45px;
}
.Whatsapp-Wrapper {
  margin-top: 37.5px;
  margin-left: 82.5px;
  display: flex;
  width: 270px;
}
.NoWhatsapp {
  margin-top: 7.5px;
  margin-left: 17.5px;
  width: 211px;
  height: 29px;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  color: #ffffff;
}
.Phone-Wrapper {
  margin-top: 39.75px;
  margin-left: 93.75px;
  display: flex;
  width: 270px;
}
.phoneIcon {
  width: 40.5px;
  height: 40.5px;
}

.NoPhone {
  margin-top: 5.25px;
  margin-left: 33.75px;
  width: 192px;
  height: 29px;
  font-weight: 400;
  font-size: 24px;
  line-height: 29px;
  color: #ffffff;
}

.social {
  margin-top: 44px;
  margin-left: 75.12px;
  display: flex;
  justify-content: space-between;
  width: 228px;
  height: 35px;
}

.socialIcon {
  width: 34px;
  height: 34px;
}
.socialIcon:hover {
  width: 45px;
  height: 45px;
  cursor: pointer;
}

@media only screen and (max-width: 600px) {
  .Contact {
    /* align-items: center; */
    flex-direction: column;
    justify-content: space-around;
    width: 360px;
    height: 180px;
  }
  .Kfclogo {
    align-self: center;
    margin-top: 20px;
    margin-left: 20px;
    width: 80px;
    height: 22px;
  }
  .Whatsapp-Wrapper {
    margin-top: 10px;
    margin-left: 20px;
    height: 50px;
  }

  .whatsapp {
    width: 25px;
    height: 25px;
  }
  .NoWhatsapp {
    margin-top: 0px;
    width: 211px;
    height: 29px;
    font-size: 18px;
  }
  .Phone-Wrapper {
    margin-top: -10px;
    margin-left: 20px;
    display: flex;
    width: 270px;
  }
  .phoneIcon {
    width: 25px;
    height: 25px;
  }
  .NoPhone {
    margin-top: 0px;
    margin-left: 15px;
    width: 192px;
    height: 29px;
    font-weight: 400;
    font-size: 18px;
    line-height: 20px;
  }
  .social {
    align-self: center;
    margin-top: -10px;
    margin-left: 10px;
    width: 228px;
    height: 35px;
  }
  .socialIcon {
    width: 25px;
    height: 25px;
  }
}
