.Footer {
  position: relative;
  display: flex;
  width: 1440px;
  height: 422px;
  background: #202329;
  overflow: hidden;
}
.footerImgWrapper {
  box-sizing: border-box;
  display: flex;
  width: 460px;
  height: 464px;
  right: 7px;
  top: 14px;
  z-index: 1;
}

.footerImg {
  position: absolute;
  top: -5px;
  width: 460px;
  height: 464px;
  right: 15px;
  object-fit: contain;
}

.FooterFirst {
  margin-left: 135px;
}
.FooterBlock {
  margin-right: 30px;
  margin-top: 52px;
  width: 160px;
  height: 30px;
  display: flex;
  flex-direction: column;
}
.FooterBigTitle {
  margin-bottom: 25px;
  font-family: "Manrope";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  font-feature-settings: "liga" off;
  color: #ffffff;
}
.FooterBigTitle:hover {
  text-decoration: underline;
  cursor: pointer;
}
.FooterSmallTitle {
  margin-top: -4px;
  font-family: "Manrope";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 26px;
  font-feature-settings: "liga" off;
  color: #d9dbe1;
}
.FooterSmallTitle:hover {
  text-decoration: underline;
  cursor: pointer;
}

.FooterEmail {
  margin-top: 42px;
  width: 255px;
  height: 94px;
}
.FooterEmailText {
  font-weight: 500;
  font-size: 20px;
  line-height: 30px;
  font-feature-settings: "liga" off;
  color: #ffffff;
}

.FooterEmailInput {
  margin-top: 4px;
  width: 255px;
  height: 40px;
  border-radius: 5px;
  display: flex;
  align-items: center;
  background-color: rgb(77, 79, 84);
  border: none;
  font-family: "Manrope";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  font-feature-settings: "liga" off;
  color: #d9dbe1;
}
.FooterEmailInput:hover {
  box-shadow: 2px -2px 2px rgba(247, 241, 241, 0.767),
    -2px 2px 2px rgba(247, 241, 241, 0.767),
    2px 2px 2px rgba(247, 241, 241, 0.767),
    -2px -2px 2px rgba(247, 241, 241, 0.767);
}

.go {
  width: 16.5px;
  height: 16.5px;
  position: absolute;
  top: 123.75px;
  right: 682.75px;
}
.go:hover {
  cursor: pointer;
}

.copyRight {
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  font-feature-settings: "liga" off;
  color: #d9dbe1;
  position: absolute;
  left: 135px;
  bottom: 23px;
}

@media only screen and (max-width: 600px) {
  .Footer {
    width: 360px;
    height: 220px;
    flex-direction: column;
  }
  .footerImgWrapper {
    width: 150px;
    height: 151px;
    right: 7px;
    top: 14px;
    z-index: 1;
  }
  .footerImg {
    width: 150px;
    height: 151px;
  }
  .FooterFirst {
    margin-left: 0px;
  }
  .FooterBlock {
    margin-right: 0px;
    margin-top: 10px;
    width: 320px;
    height: 30px;
    flex-direction: row;
  }
  .FooterBigTitle {
    margin: 0 10px 10px 10px;
    font-size: 15px;
    line-height: 15px;
  }
  .FooterSmallTitle {
    margin-top: 3px;
    font-size: 10px;
    line-height: 10px;
    width: 70px;
  }
  .FooterEmail {
    margin-left: 10px;
    margin-top: 0px;
    width: 255px;
    height: 94px;
  }
  .FooterEmailText {
    font-size: 15px;
    line-height: 20px;
  }
  .FooterEmailInput {
    margin-top: 4px;
    width: 230px;
    height: 32px;
    font-size: 12px;
    line-height: 19px;
  }
  .go {
    width: 16.5px;
    height: 16.5px;
    position: absolute;
    top: 138.75px;
    left: 215px;
  }
  .copyRight {
    font-size: 12px;
    line-height: 18px;
    left: 135px;
    bottom: 23px;
  }
}
