.FirstWrapper {
  display: flex;
  width: 1440px;
}
.Description {
  /* position: relative; */
  width: 1440px;
  z-index: 2;
  overflow: hidden;
}

.ellips1 {
  position: absolute;
  width: 347px;
  height: 347px;
  left: 0px;
  top: 69px;
  border-radius: 50%;
  box-sizing: border-box;
  z-index: 1;
}
.ellips2 {
  position: absolute;
  width: 206px;
  height: 206px;
  left: 460px;
  top: -103px;
  z-index: 6;
}

.ellips3 {
  position: absolute;
  left: 1305px;
  top: -37px;
  width: 347px;
  height: 347px;
  border-radius: 50%;
  box-sizing: border-box;
  z-index: 1;
}

@media only screen and (max-width: 600px) {
  .ellips2 {
    display: none;
  }

  .ellips3 {
    display: none;
  }
  .FirstWrapper {
    display: flex;
    width: 360px;
  }
}
