.NavBar {
  position: fixed;
  top: 0;
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  padding: 27px 144px 0 135px;
  display: flex;
  height: 50px;
  align-items: center;
  background-color: #ff4029;
  z-index: 4;
}

.logo {
  width: 139px;
  height: 39px;
}

.links {
  width: 370px;
  height: 22px;
  display: flex;
  justify-content: space-between;
  margin-left: 471px;
}

.link {
  font-weight: 500;
  font-size: 18px;
  line-height: 22px;
  color: #ffffff;
  text-decoration: none;
}
.link:hover {
  text-decoration: underline;
  cursor: pointer;
}

.account-basket {
  position: relative;
  width: 110px;
  display: flex;
  justify-content: space-between;
  margin-left: 71px;
}

.avatar-wrapper {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 44px;
  height: 44px;
  background-color: white;
  border-radius: 22px;
}
.avatar-wrapper:hover {
  box-shadow: 2px -2px 2px rgba(247, 241, 241, 0.767),
    -2px 2px 2px rgba(247, 241, 241, 0.767),
    2px 2px 2px rgba(247, 241, 241, 0.767),
    -2px -2px 2px rgba(247, 241, 241, 0.767);
  cursor: pointer;
}

.counter {
  position: relative;
  background-color: black;
  top: -18px;
  right: -12px;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  z-index: 10;
}

.num {
  position: absolute;
  top: -3px;
  right: 4.6px;
  color: #fff;
  z-index: 10;
  font-size: 9px;
}

.account {
  width: 16px;
  height: 21px;
}
.basket {
  position: absolute;
  width: 20px;
  height: 17.56px;
}
.Description {
  display: block;
}

.SmallCart {
  z-index: 10;
  position: absolute;
  top: 50px;
  right: 0px;
  width: 220px;
  border-radius: 10px;
  border: 1px solid rgba(175, 171, 171, 0.767);
  min-height: 50px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
  box-shadow: 2px 2px 2px rgba(214, 211, 211, 0.767);
}

.SmallCartTitleWrapper {
  width: 200px;
  display: flex;
  margin-bottom: -5px;
  justify-content: space-between;
}

.SmallCartProduct {
  display: flex;
  padding-left: 10px;
  width: 220px;
  height: 40px;
}
.SmallCartTitles {
  margin-top: 10px;
}
.SmallCartButtonWrapper {
  position: absolute;
  right: 12px;
  margin-top: 12px;
  /* margin-left: -15px; */
}
.MyCart {
  margin-top: 3px;
  margin-left: 10px;
  color: rgb(59, 58, 58);
  align-self: flex-start;
  font-size: 12px;
  font-weight: bold;
}
.SmallCartClose {
  color: rgb(121, 121, 121);
  align-self: flex-end;
  font-size: 12px;
}
.SmallCartClose:hover {
  color: #ff4029;
  cursor: pointer;
}
.SmallCartButton {
  background-color: #fff;
  margin-left: 7px;
  border: none;
  width: 10px;
  color: rgb(121, 121, 121);
}
.SmallCartGo {
  text-decoration: none;
  margin-bottom: 5px;
  margin-right: 5px;
  color: black;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 70px;
  height: 18px;
  align-self: flex-end;
  justify-self: flex-end;
  font-size: 12px;
  border: 1px solid rgb(187, 179, 179);
  background-color: rgb(236, 226, 226);
  border-radius: 5px;
}

.SmallCartGo {
  cursor: pointer;
}

.HoverBlue:hover {
  color: rgb(0, 120, 190);
  cursor: pointer;
}
.HoverRed:hover {
  color: red;
  cursor: pointer;
}

.SmallCartProductFont {
  margin-left: 5px;
  font-size: 12px;
  color: rgb(121, 121, 121);
}

@media only screen and (max-width: 600px) {
  .NavBar {
    max-width: 360px;
    padding: 0;
    position: fixed;
    top: 0;
  }
  .logo {
    margin-top: 50px;
    margin-left: 100px;
  }
  .account {
    display: none;
  }

  .avatar-wrapper {
    display: none;
  }
  .basket-wrapper {
    position: absolute;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 44px;
    height: 44px;
    background-color: white;
    border-radius: 22px;
  }
  .account-basket {
    margin-left: -600px;
  }
}
