.bucket {
  position: absolute;
  width: 440.5px;
  height: 446.9px;
  left: 740.93px;
  top: 262.89px;
  transform: rotate(-4.04deg);
  /* background: linear-gradient(123.49deg, #ff4029 0.57%, #f02228 104.89%); */
  z-index: 2;
}

.leg {
  position: absolute;
  width: 341px;
  height: 256px;
  left: 700px;
  top: 407.7px;
  z-index: 1;
}

.fries {
  position: absolute;
  width: 206.87px;
  height: 229.86px;
  left: 1028.59px;
  top: 455px;
  transform: rotate(4.16deg);
  z-index: 3;
}

.price-wrapper {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 194px;
  height: 194px;
  left: 1037px;
  top: 200.7px;
  border-radius: 50%;
  background: #ffffff;
  z-index: 1;
}

.price {
  display: flex;
  flex-direction: column;
  justify-items: center;
  width: 179px;
  height: 179px;
  border: 4px dashed #dadada;
  box-sizing: border-box;
  border-radius: 50%;
}

.currency {
  position: relative;
  top: 10px;
  right: -40px;
  width: 24px;
  height: 22px;
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  color: #f73d27;
}
.priceNum {
  margin-top: -20px;
  margin-left: 30px;
  width: 147px;
  height: 77px;
  padding-bottom: 20px;
  font-weight: 800;
  font-size: 64px;
  line-height: 77px;
  color: #f73d27;
}

.decimal {
  font-size: 20px;
  font-weight: 1000;
}

@media only screen and (max-width: 600px) {
  .bucket {
    display: none;
  }
  .leg {
    display: none;
  }
  .fries {
    display: none;
  }
  .price-wrapper {
    display: none;
  }
  .price {
    display: none;
  }
  .currency {
    display: none;
  }
  .priceNum {
    display: none;
  }
  .decimal {
    display: none;
  }
}
