.ForthSection {
  position: relative;
  width: 1440px;
  height: 841px;
  background: #eed5d5;
}

.ForthTitleDiv {
  margin-top: 130px;
  margin-left: 280px;
  width: 400px;
  height: 100px;
}

.ForthBigTitle {
  font-weight: 700;
  font-size: 40px;
  line-height: 48px;
  color: #000;
}

.ForthSmallTitle {
  margin-top: -15px;
  font-weight: 500;
  font-size: 17px;
  color: #000;
}

.quoteOne {
  position: absolute;
  top: 290px;
  left: 340px;
  height: 396px;
  width: 320px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 7px 10px 24px rgba(170, 168, 164, 0.22);
}
.quoteOneParag {
  margin-left: 40px;
  margin-top: 30px;
  height: 256px;
  width: 220px;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  color: #000;
}

.quoteBigTitle {
  margin-left: 40px;
  font-weight: 800;
  font-size: 14px;
}
.quoteSmallTitle {
  margin-left: 40px;
  font-weight: 500;
  font-size: 14px;
}

.quoteTwo {
  position: absolute;
  top: 168px;
  left: 716px;
  height: 236px;
  width: 390px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 7px 10px 24px rgba(170, 168, 164, 0.22);
}
.quoteTwoParag {
  margin-left: 40px;
  margin-top: 30px;
  height: 120px;
  width: 320px;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  color: #000;
}

.quoteThree {
  position: absolute;
  top: 440px;
  left: 716px;
  height: 250px;
  width: 320px;
  background-color: white;
  border-radius: 5px;
  box-shadow: 7px 10px 24px rgba(170, 168, 164, 0.22);
}
.quoteThreeParag {
  margin-left: 40px;
  margin-top: 30px;
  height: 128px;
  width: 192px;
  font-weight: 500;
  font-size: 20px;
  line-height: 28px;
  color: #000;
}
@media only screen and (max-width: 600px) {
  .ForthSection {
    margin-top: -130px;
    width: 360px;
    height: 760px;
  }
  .ForthTitleDiv {
    padding-top: 50px;
    margin-left: 20px;
    width: 340px;
    height: 100px;
  }
  .ForthBigTitle {
    text-align: center;
    font-size: 25px;
    line-height: 36px;
  }
  .ForthSmallTitle {
    margin-left: 60px;
    margin-top: -15px;
    font-size: 15px;
  }
  .quoteSmallTitle {
    display: none;
  }
  .quoteBigTitle {
    margin-left: 20px;
    font-weight: 800;
    font-size: 14px;
  }
  .quoteOne {
    top: 220px;
    left: 20px;
    height: 280px;
    width: 220px;
    z-index: 1;
  }
  .quoteOneParag {
    margin-left: 10px;
    margin-top: 30px;
    height: 160px;
    width: 200px;
    font-size: 15px;
    line-height: 20px;
  }
  .quoteTwo {
    z-index: 2;
    top: 490px;
    left: 40px;
    height: 236px;
    width: 300px;
    background-color: rgb(236, 231, 231);
  }
  .quoteTwoParag {
    margin-left: 40px;
    margin-top: 30px;
    height: 120px;
    width: 220px;
    font-size: 15px;
    line-height: 20px;
  }
  .quoteThree {
    display: none;
  }
}
