.ThirdSection {
  width: 1440px;
  height: 326px;
  background: linear-gradient(262.18deg, #ffb737 -0.11%, #ffbe4b 97.74%);
  z-index: -2;
}
.PhoneWrapper {
  position: relative;
  display: flex;
}
.phone1 {
  position: absolute;
  width: 247px;
  height: 495px;
  left: 134px;
  top: -48px;
  z-index: -1;
}
.phone2 {
  position: absolute;
  width: 213px;
  height: 429px;
  left: 322px;
  top: -15px;
  z-index: -0;
}
.ThirdSectionTexts {
  display: flex;
  height: 226px;
  flex-direction: column;
  margin-left: 611px;
}
.ThirdSectionParag {
  width: 460px;
  height: 144px;
  font-weight: 700;
  font-size: 40px;
  line-height: 48px;
  text-transform: capitalize;
  color: #ffffff;
}
.Download {
  width: 400px;
  display: flex;
}

.ios {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 39px;
  width: 93.5px;
  border-radius: 5px;
  background-color: #000;
  padding-left: 41px;
}
.apple {
  position: absolute;
  width: 19.72px;
  height: 16.52px;
  left: 17.13px;
  top: 13.1px;
}

.stem {
  position: absolute;
  width: 4.32px;
  height: 6.08px;
  left: 25.95px;
  top: 7.2px;
}

.iosSmallTitle {
  color: #fff;
  font-size: 9px;
  font-weight: 300;
}
.iosBigTitle {
  margin-top: -10px;
  margin-bottom: 20px;
  color: #fff;
  font-size: 14px;
  font-weight: 400;
}

.play {
  margin-left: 40px;
  height: 40px;
  width: 135px;
}

@media only screen and (max-width: 600px) {
  .ThirdSection {
    width: 360px;
    height: 150px;
  }
  .phone1 {
    position: absolute;
    width: 150px;
    height: 250px;
    left: 20px;
    top: -48px;
    z-index: 2;
  }
  .phone2 {
    position: absolute;
    width: 120px;
    height: 200px;
    left: 134px;
    top: -15px;
    z-index: 1;
  }
  .ThirdSectionTexts {
    margin-left: 261px;
  }

  .ThirdSectionParag {
    display: none;
  }
  .Download {
    margin-top: 30px;
    margin-right: 15px;
    width: 55px;
    flex-direction: column;
  }

  .ios {
    height: 25px;
    width: 52px;
    border-radius: 5px;
    background-color: #000;
    padding-left: 41px;
  }
  .apple {
    width: 13px;
    height: 11px;
    left: 17.13px;
    top: 7.1px;
  }

  .stem {
    width: 3.5px;
    height: 4.5px;
    left: 23.95px;
    top: 2.4px;
  }

  .iosSmallTitle {
    display: none;
  }
  .iosBigTitle {
    margin-top: 7px;
    margin-left: -5px;
    font-size: 11px;
  }

  .play {
    margin-top: 10px;
    margin-left: 0px;
    height: 35px;
    width: 94px;
  }
}
